import React from 'react'
import clsx from 'clsx'

export interface LabelProps {
  /** Contenu du label (texte ou lments JSX) */
  children: React.ReactNode

  /** ID de linput associ */
  htmlFor?: string

  /** Classes personnalises */
  className?: string

  /** Dsactive visuellement le label */
  disabled?: boolean
}

/**
 *  Label  lment de formulaire bas sur le Design System
 * Full tokenis (`ds-*`), gre ltat dsactiv et la typographie DS.
 */
export const Label: React.FC<LabelProps> = ({
  children,
  htmlFor,
  className = '',
  disabled = false
}) => {
  return (
    <label
      htmlFor={htmlFor}
      className={clsx(
        'block select-none text-ds-font-size-sm font-ds-font-weight-medium',
        disabled
          ? 'text-ds-text-muted cursor-not-allowed'
          : 'text-ds-text-secondary',
        'mb-ds-space-xs',
        className
      )}
    >
      {children}
    </label>
  )
}

Label.displayName = 'Label'
